<template>
  <div>
    <el-row type="flex" class="" justify="center">
      <el-col :span="11"><div class="row-bg title-center"><hr class="title-line"/></div></el-col>
      <el-col :span="2"><div class="grid-content title-center center"></div></el-col>
      <el-col :span="11"><div class="row-bg title-center"><hr class="title-line"/></div></el-col>
    </el-row>
    <el-row type="flex" justify="center">
      <el-col :span="6"><div class="title-center center"><el-button type="primary" @click="addTimeline"></el-button></div></el-col>
    </el-row>

    <el-row type="flex" class="" justify="center">
      <el-col :span="11"><div class="row-bg title-center"><hr class="title-line"/></div></el-col>
      <el-col :span="3"><div class="grid-content title-center center"></div></el-col>
      <el-col :span="11"><div class="row-bg title-center"><hr class="title-line"/></div></el-col>
    </el-row>
    <el-table :data="tableData" stripe border style="width: 100%">
      <el-table-column align="center" label="" width="180">
        <template scope="scope">
          <el-icon name="time"></el-icon>
          <span style="margin-left: 10px">{{ scope.row.date }}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="">
        <template scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>: {{ scope.row.name }}</p>
            <p>: {{ scope.row.address }}</p>
            <div slot="reference" class="name-wrapper">
              <span>{{ scope.row.name }}</span>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" width="180">
        <template scope="scope">
          <el-button type="primary" icon="edit" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button type="danger" icon="delete" @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
        <pagination :totalList="listTotal" style="margin-top:20px;text-align:center;"></pagination>
  </div>
</template>

<script>
import pagination from '@/components/Pagination'
export default {
  name: 'manageArticles',
  data () {
    return {
             listTotal: {
               totalElements: 12,
               content: []
             },
     options: [{
       value: '1',
       label: ''
     }, {
       value: '2',
       label: ''
     }, {
       value: '3',
       label: ''
     }, {
       value: '4',
       label: 'javascript'
     }, {
       value: '5',
       label: 'ES6'
     }],
     value4: '',
     options4: [],
     value9: [],
     list: [],
     loading: false,
     states: ["Alabama", "Alaska", "Arizona",
     "Arkansas", "California", "Colorado",
     "Connecticut", "Delaware", "Florida",
     "Georgia", "Hawaii", "Idaho", "Illinois",
     "Indiana", "Iowa", "Kansas", "Kentucky",
     "Louisiana", "Maine", "Maryland",
     "Massachusetts", "Michigan", "Minnesota",
     "Mississippi", "Missouri", "Montana",
     "Nebraska", "Nevada", "New Hampshire",
     "New Jersey", "New Mexico", "New York",
     "North Carolina", "North Dakota", "Ohio",
     "Oklahoma", "Oregon", "Pennsylvania",
     "Rhode Island", "South Carolina",
     "South Dakota", "Tennessee", "Texas",
     "Utah", "Vermont", "Virginia",
     "Washington", "West Virginia", "Wisconsin",
     "Wyoming"],
      tableData:[{
              date: '2016-05-02',
              name: 'array.splice(0, array.length) ',
              address: ' 1518 '
            }, {
              date: '2016-05-04',
              name: '7 Layui2.0',
              address: ' 1517 '
            }, {
              date: '2016-05-01',
              name: '',
              address: ' 1519 '
            }, {
              date: '2016-05-03',
              name: '',
              address: ' 1516 '
            }, {
              date: '2016-05-03',
              name: '',
              address: ' 1516 '
            }, {
              date: '2016-05-03',
              name: '',
              address: ' 1516 '
            }, {
              date: '2016-05-03',
              name: '',
              address: ' 1516 '
            }]
             }
  },
  mounted() {
    this.list = this.states.map(item => {
      return { value: item, label: item };
    });
  },
  components: {
      pagination
    },
  methods: {
    remoteMethod(query) {
      if (query !== '') {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.options4 = this.list.filter(item => {
            return item.label.toLowerCase()
              .indexOf(query.toLowerCase()) > -1;
          });
        }, 200);
      } else {
        this.options4 = [];
      }
    },
    handleEdit(index, row) {
      this.$router.push({name: 'timeLineadd'})
      console.log(index, row);
    },
    handleDelete(index, row) {
      console.log(index, row);
    },
    addTimeline () {
      this.$router.push({path: '/timeLineadd'})
    }
  }
}
</script>

<style scoped lang="scss">
.el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
    .center {
     text-align: center;
    }
    .display {
     display: block;
    }
  }
  .row-bg {
    padding: 15px 0;
  }
  .title-center {
    font-size: 20px;
    line-height: 36px;
  }
  .title-line {
    margin:0px;
    height:1px;
    border:0px;
    background-color:#e2e2e2;
  }
</style>



