import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Login from '@/views/Login'
import Home from '@/views/Home'
import Form from '@/views/nav1/Form'
import timeLine from '@/views/articles/timeLine'
import articlesManage from '@/views/articles/articlesManage'
import selectExcel from '@/views/excel/selectExcel'
import articlesAdd from '@/views/articles/articlesAdd'
import text from '@/views/articles/text'
import text1 from '@/views/articles/text1'
import timeLineadd from '@/views/articles/timeLineadd'
import Dashboard from '@/views/dashboard'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      component: Login,
      name: '',
      hidden: true
    },
    {
      path: '/',
      name: 'Hello',
      component: Hello,
      hidden: true
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      hidden: true
    },
    {
       path: '/',
       component: Home,
       name: '',
       iconCls: 'fa fa-address-card',
       leaf: true,//
       children: [
           { path: '/dashboard', component: Dashboard, name: '' }
       ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      iconCls: 'el-icon-message',
      children: [
          { path: '/articlesManage', component: articlesManage, name: '' },
          { path: '/timeLine', component: timeLine, name: '' },
          { path: '/timeLineadd', component: timeLineadd, name: '',hidden: true },
          { path: '/articlesAdd', component: articlesAdd, name: '',hidden: true },
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      iconCls: 'fa fa-id-card-o',
      children: [
          { path: '/name', component: Form, name: 'Form' }
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      iconCls: 'fa fa-id-card-o',
      children: [
          { path: '/name', component: Form, name: 'Form' }
      ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/dashboard' }
    }
  ]
})



