<template>
  <div class="app-container">
    <el-button style='margin-bottom:20px;float:right' type="primary" icon="document" @click="handleDownload">excel</el-button>

    <el-table :data="list" v-loading.body="listLoading" element-loading-text="" border fit highlight-current-row @selection-change="handleSelectionChange" ref="multipleTable">
      <el-table-column type="selection" align="center"></el-table-column>

      <el-table-column align="center" label='ID' width="95">
        <template scope="scope">
          {{scope.$index}}
        </template>
      </el-table-column>

      <el-table-column label="">
        <template scope="scope">
          {{scope.row.title}}
        </template>
      </el-table-column>

      <el-table-column label="" width="110">
        <template scope="scope">
          <span>{{scope.row.author}}</span>
        </template>
      </el-table-column>

      <el-table-column label="" width="105" align="center">
        <template scope="scope">
          {{scope.row.pageviews}}
        </template>
      </el-table-column>

      <el-table-column align="center" prop="created_at" label="" width="200">
        <template scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.display_time}}</span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>


<script>
  export default {
    data () {
      return {
        list: [
          {
            'id': '460000198209297223',
            'title': '',
            'status': 'published',
            'author': '',
            'display_time': '2015-02-02 01:46:02',
            'pageviews': 4920
          },
          {
            'id': '370000197306245787',
            'title': '',
            'status': 'draft',
            'author': '',
            'display_time': '1975-04-13 14:38:52',
            'pageviews': 4806
          },
          {
            'id': '370000197306245787',
            'title': '',
            'status': 'draft',
            'author': '',
            'display_time': '1975-04-13 14:38:52',
            'pageviews': 4806
          }
        ],
        listLoading: true,
        multipleSelection: []
      }
    },
    created () {
      this.listLoading = false
    },
    methods: {
      handleSelectionChange (val) {
        this.multipleSelection = val
      },
      handleDownload () {
        if (this.multipleSelection.length) {
          require.ensure([], () => {
            const { export_json_to_excel } = require('../../vendor/Export2Excel')
            const tHeader = ['', '', '', '', '']
            const filterVal = ['id', 'title', 'author', 'pageviews', 'display_time']
            const list = this.multipleSelection
            const data = this.formatJson(filterVal, list)
            export_json_to_excel(tHeader, data, 'excel')
            this.$refs.multipleTable.clearSelection()
          })
        } else {
          this.$message({
            message: '',
            type: 'warning'
          })
        }
      },
      formatJson (filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => v[j]))
      }
    }
  }
</script>



