import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

// 
const state = {
  count: 10,
  mainMsg: 'huanhuan'
}

//  mutations
const mutations = {
  INCREMENT (state) {
    state.count++
  },
  DECREMENT (state) {
    state.count--
  }
}

//  store 
const store = new Vuex.Store({
  actions,
  getters,
  state,
  mutations
});

export default store




